<script>
import { ImageBedUtils } from "@/utils/image-bed-utils";
import { githubApi } from "@/utils/github-api";
export default {

  mounted() {
    var that = this;
    document.addEventListener("paste", function (event){
      let clipboardData = event.clipboardData || window.clipboardData || event.originalEvent.clipboardData;
        /* 判断是否支持粘贴上传 */
        if (!clipboardData || !clipboardData.items) return alert('当前浏览器不支持粘贴上传');
        let items = clipboardData.items;
        let file = null;
        /* 判断剪切板的内容是否是桌面类型的文件 */
        if (items.length === 0) return alert('剪切板内无内容或不支持桌面文件');
        /* 开始循环剪切板的内容，判断是否是文件类型，如果是文件类型，则push */
        for (let i = 0; i < items.length; i++) {
            if (items[i].type.indexOf('image') !== -1) {
                file = items[i].getAsFile();
            }
        }
        if (!file) return alert('剪切板内无内容或不支持桌面文件');
        that.setImageInfo(new Array(file));
    })
  },
  data() {
    return {
      images: [],
      dragState:false,
      dialogImageUrl: "",
      dialogVisible: false,
    };
  },
  methods: {
    open() {
      this.$refs.file.click();
    },
    change(e) {
      var files = this.$refs.file.files;
      this.setImageInfo(files)
    },
    mouseenter(e){
      this.dragState = true;
    },
    mouseleave(e){
      this.dragState = false;
    },
    dragover(e) {
      e.preventDefault();
    },
    drop(e) {
      e.preventDefault();
      this.dragState = false;
      var files = e.dataTransfer.files;
      this.setImageInfo(files)
    },
    formatBytes(size){
      return ImageBedUtils.formatBytes(size)
    },
    image2svg(size){
      return ImageBedUtils.image2svg(size)
    },
    async setImageInfo(fs){
      if(fs.length){
        for (var i = 0; i < fs.length; i++) {
          var name = new Date().getTime()+".svg"
          let svg = await this.image2svg(fs[i])
          this.images.push({name,size:svg.length,state:0})
          await this.upLoadMediaFile(name,svg)
        }
      }
    },
    async del(item, index){
      if (!confirm("确定删除？")) return;
      if (item.state===1){
        this.$loading.show();
        const res = await githubApi.removeFile(`db/images/${item.name}`);
        this.$loading.hide();
        if (res.status !== "OK") {
          this.$message.error(res.msg);
        } else {
          this.$message(res.msg);
          this.images.splice(index,1);
        }
      } else {
        this.images.splice(index,1);
        this.$message("删除成功！");
      }
    },
    async upLoadMediaFile(name,content){
      this.$loading.show();

      const res = await githubApi.upLoadMediaFile(
        `db/images/${name}`,
        content
      );
      this.$loading.hide();
      if (res.status !== "OK") {
        this.$message.error(res.msg);
        this.images.forEach(element => {
          if(element.name == name){
            element.state = 2
          }
        });
      } else {
        this.images.forEach(element => {
          if(element.name == name){
            element.state = 1
          }
        });
      }
    }
    
  },
};
</script>
